
const answerReducer = (state = [], action) => {
  switch(action.type) {
    case 'ADD_ANSWER':
    const newAns = action.data
      return [...state, newAns]
    case 'REMOVE_ANSWER':
    const numberOfAnswers = state.length
    const answers = state.slice(0, numberOfAnswers - 1)
      return answers
    case 'RESET_ANSWERS':
      return action.data
    default:
      return state
  }
}

export const addAnswer = (answer) => {
  return async (dispatch) => {
  dispatch({
      type: 'ADD_ANSWER',
      data: answer
    })
  }
}

export const removeAnswer = () => {
  return async (dispatch) => {
  dispatch({
      type: 'REMOVE_ANSWER'
    })
  }
}

export const resetAnswers = () => {
  return async (dispatch) => {
  dispatch({
      type: 'RESET_ANSWERS',
      data: []
    })
  }
}

export default answerReducer



