import questionService from './../services/questions'

const questionReducer = (state = [], action) => {
  switch(action.type) {
    case 'INIT_QUESTIONS':
      return action.data
    default:
      return state
  }
}


export const initQuestions = (language) => {
  return async (dispatch) => {
    const questions = await questionService.getAll(language)
    dispatch({
      type: 'INIT_QUESTIONS',
      data: questions
    })
  }
}

export default questionReducer



