import React from 'react'
import {Table} from 'react-bootstrap'
import {connect} from 'react-redux'
import { getResults } from '../reducers/resultReducer'


class ResultList extends React.Component {
  componentWillMount = async () => {
    await this.props.getResults(this.props.answers)
    this.props.history.push('/quiz/results')

  }

  render(){
      if(this.props.results === undefined | this.props.results.length === 0) {
        return <div></div>
      }
      const results = this.props.results
      const header = this.props.language === "EN" ? <HeaderEnglish/> : <HeaderFinnish/>
      return (
        <div>
          {header}
          <Table>
            <tbody>
            {results.map(a =>
              <tr key = {a.model}>
                <td>
                  <a href={a.link}>
                    <img src={a.photo} alt={a.model} style={{width: 250, marginRight:5}} />
                    <br/>
                    {a.model} &nbsp;({a.score} % match)
                  </a>
                </td>
              </tr>
            )}
            </tbody>
          </Table>
        </div>
      )
  }
}

const HeaderEnglish = () => {
  return(
    <div>
      <h2 style={{marginBottom:20}}>Results</h2>
      <h5 style={{marginBottom:20}}>Here are the recommended models listed from most suitable to least matching.</h5>
    </div>
  )
}

const HeaderFinnish = () => {
  return(
    <div>
      <h2 style={{marginBottom:20}}>Tulokset</h2>
      <h5 style={{marginBottom:20}}>Allaolevat mallit on järjestetty vastaustesi perusteella sopivimmasta vaihtoehdosta alkaen.</h5>
    </div>
  )
}

const mapStateToProps = (state) => {
    return {
      questions: state.questions,
      answers: state.answers,
      results: state.results,
      language: state.language
    }
}


export default connect(
  mapStateToProps,{getResults}
)(ResultList)



