import React from 'react'
import {Table} from 'react-bootstrap'
import Answer from './Answer'

const Question = ({ question, answerHandler }) => {
  if(question === undefined) {
    return <div></div>
  }

  return (
    <div >
      <h2 style={{paddingBottom: 10}}>{question.question}</h2>
      <Table>
        <tbody>
        {question.answers.map(answer =>
          <tr key = {answer.value}>
            <td onClick = {() => answerHandler(question.attribute, answer)}><Answer answer = {answer} handler = {answerHandler}/></td>
          </tr>
        )}
        </tbody>
      </Table>
    </div>
  )
}

export default Question



