const languageReducer = (state = "EN", action) => {
  switch(action.type) {
    case 'CHANGE_LANGUAGE':
      return action.data
    default:
      return state
  }
}

export const changeLanguage = (language) => {
  return async (dispatch) => {
  dispatch({
      type: 'CHANGE_LANGUAGE',
      data: language
    })
  }
}

export default languageReducer



