import React from 'react'
import '../App.css';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'

const Home = (props) => {
    return(
      <div className="homestyle">
        <Title language={props.language}/>
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Pelago-Brooklyn.jpg" alt="Brooklyn" />
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Hanko-Street.jpg" alt="Hanko Street" />
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Stavanger_Commuter_1.jpg" alt="Stavanger Commuter" />
        <Start language={props.language}/>
        <Info language={props.language}/>
      </div>
    )
}

const Start = (props) => {
const text = props.language === "EN" ? "Start" : "Aloita"
  return(
    <Link style={{color:"black"}} to="/quiz">
      <div className="startBlock">
        <p>{text}</p>
      </div>
    </Link>
  )
}

const Title = (props) => {
  const title = props.language == "EN" ? "Welcome to Bike Assistant" : "Tervetuloa Pyöräsuosittelijaan"
  return(
      <h2>{title}</h2>
  )
}

const Info = (props) => {
  const upperInfo = props.language == "EN" ? "The idea is to give you a recommendation so that you have more than a hunch of what you are after." : "Vastaa kymmeneen kysymykseen ja katso, mikä malli vastaa parhaiten mieltymyksiäsi."
  const lowerInfo = props.language == "EN" ? "[ If you find it challenging answering to a question, feel free to skip it. ]" : "[ Voit hypätä kysymyksen yli, jos siihen vastaaminen tuntuu vaikealta. ]"
  return(
    <div>
      <p className="home-upperBlock">{upperInfo}</p>
      <p className="home-lowerBlock">{lowerInfo}</p>
    </div>
  )
}

export default Home



