import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Link, Redirect } from 'react-router-dom'
import {connect} from 'react-redux'

import QuestionList from './components/QuestionList'
import {initQuestions} from './reducers/questionReducer'
import {changeLanguage} from './reducers/languageReducer'

import ResultList from './components/ResultList'
import Home from './components/Home'
import {Table} from 'react-bootstrap'
import logo from './pelagologo.png'

class App extends React.Component {

  componentWillMount = async () => {
    await this.props.initQuestions(this.props.language)
  }

  render() {
    /*<Table>
      <tbody>
        <tr>
          <td className="header-logo"><img src={logo} alt="Pelago Bicycles"/></td>
          <td className="languageSelection">
            <p><a style={eng} onClick={this.languageChange}>EN</a> / <a style={fi} onClick={this.languageChange}>FI</a></p>
          </td>
        </tr>
      </tbody>
    </Table>
    <div className="languageSelection">
      <p><a style={eng} onClick={this.languageChange}>EN</a> / <a style={fi} onClick={this.languageChange}>FI</a></p>
    </div>
    */
    const eng = this.props.language === "EN" ? {backgroundColor : 'lightgray'} : {}
    const fi = this.props.language === "FI" ? {backgroundColor : 'lightgray'} : {}
    return (
      <div className="App">
        <div className='container'>
        <Table style={{marginBottom: 1, marginTop: 1}}>
          <tbody>
            <tr>
              <td className="header-logo"><img src={logo} alt="Pelago Bicycles"/></td>
              <td className="languageSelection">
                <p><a style={eng} onClick={this.languageChange}>EN</a> | <a style={fi} onClick={this.languageChange}>FI</a></p>
              </td>
            </tr>
          </tbody>
        </Table>
            <Router>
              <div>
                <Route path="/quiz" render={() => <TopNavi language={this.props.language} />} />
                <Route exact path="/" render={() => <Home language={this.props.language} />} />
                <Route exact path="/quiz" render={() => <QuestionList/> } />
                <Route exact path="/quiz/results" render={({history}) =>
                  this.props.questions ? <ResultList history={history} /> : <Redirect to="/" />} />
              </div>
            </Router>
        </div>
        <p className="App-footer">All rights reserved.</p>
      </div>
    )
  }

  languageChange = async (e) => {
    const language = e.target.innerHTML
    await this.props.changeLanguage(language)
    await this.props.initQuestions(this.props.language)
    }
}

const TopNavi = (props) => {
  const text = props.language === "EN" ? "FRONTPAGE" : "ETUSIVU"
  return(
    <div>
      <Table>
        <tbody>
          <tr>
            <td><Link style={{color: "#222"}} to="/"><h5 style={{paddingTop: 10, fontSize:'medium'}}>[ {text} ]</h5></Link></td>
          </tr>
        </tbody>
      </Table>
    </div>
  )
}
const mapStateToProps = (state) => {
    return {
      questions: state.questions,
      answers: state.answers,
      results: state.results,
      language: state.language
    }
}

export default connect(
  mapStateToProps,
  {initQuestions,
  changeLanguage}
)(App)



