import resultService from './../services/results'

const resultReducer = (state = [], action) => {
  switch(action.type) {
    case 'GET_RESULTS':
      return action.data
      case 'RESET_RESULTS':
        return action.data
    default:
      return state
  }
}

export const getResults = (answers) => {
  return async (dispatch) => {
    const results = await resultService.showResults(answers)
  dispatch({
      type: 'GET_RESULTS',
      data: results
    })
  }
}

export const resetResults = () => {
  return async (dispatch) => {
    const results = []
  dispatch({
      type: 'RESET_RESULTS',
      data: results
    })
  }
}
export default resultReducer



